button {
  background: transparent;
  border: 0;
  cursor: pointer;
}

.containerForm {
  display: flex;
  justify-content: center;
  align-items: center;
}

.login_card_img {
  width: 260px;
  padding: 5px;
  margin-left: 90px;
  margin-bottom: 10px;
}

.login_form {
  width: 100%;
  margin: 0;
  display: grid;
}

.login_form input.control::placeholder {
  color: #9d9fbd;
}

.control {
  border: 1.5px solid #dfe1f0;
  outline: none;
  width: 100%;
  height: 50px;
  padding: 0 16px;
  background: #f6f7ff;
  color: inherit;
  border-radius: 6px;
  margin: 8px 0;
  font-family: 'Roboto Condensed', sans-serif;
  text-align: left;
  font-size: 18px;
  transition: 0.4s;
}
.select_status {
  margin: 8px 0;
}

button.control {
  cursor: pointer;
  width: 100%;
  height: 56px;
  padding: 0 16px;
  background: linear-gradient(120deg, #f68251, #fbefbb);
  color: #000000;
  border: 0;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  letter-spacing: 2px;
  transition: all 0.375s;
}

button.control:hover {
  background: #555894;
}

button.control:active {
  transition: 0.01s;

  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5);
}

.strength {
  text-align: left;
  height: 25px;
  text-transform: capitalize;
  color: #f4f4f4;
}

.errorRegister {
  font-size: 20px;
  color: #ff0000;
}

.registerName {
margin-bottom: 20px;
font-size: 60px;
}