.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    opacity: 0;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
    z-index: 1;
}

.modal__content {
    padding: 50px;
    border-radius: 12px;
    background-color: #a8a9b3;
    max-width: 1100px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    font-family: 'Bruno Ace SC', cursive;
    font-size: 18px;
    font-weight: 600;
    z-index: 2;
}
