.sport_container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.main {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 50px;
}

.img_container {
  width: 350px;
  height: 350px;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 30px;
  border: 3px solid #fbefbb;
}

.img_sportsmen {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.card {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 700px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  padding: 32px;
  overflow: hidden;
  background: rgba(124, 130, 138, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  color: #fbefbb;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}

.content .heading {
  font-weight: 700;
  font-size: 40px;
  font-family: 'Roboto Condensed', sans-serif;
}

.content .para {
  line-height: 1.5;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 22px;
}

.btn5 {
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
  cursor: pointer;
  outline: none;
  border: 1px solid #f68251;
  background-color: #232423;
  padding: 0.3em 1.2em;
  border-radius: 30px;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1.3rem;
  font-weight: 550;
  color: #ffffff;
  background-size: 100% 100%;
  box-shadow: 0 0 0 4px #232423 inset;
}

.btn5:hover {
  background-image: linear-gradient(
    55deg,
    transparent 10%,
    #161616 10% 20%,
    transparent 20% 30%,
    #161616 30% 40%,
    transparent 40% 50%,
    #161616 50% 60%,
    transparent 60% 70%,
    #161616 70% 80%,
    transparent 80% 90%,
    #161616 90% 100%
  );
  animation: background 3s linear infinite;
}

.modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.input {
  width: 400px;
  max-width: 320px;
  height: 45px;
  padding: 12px;
  margin-top: 5px;
  border-radius: 12px;
  border: 1.5px solid #f68251;
  outline: none;
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
  box-shadow: 0px 0px 20px -18px;
}

.input:hover {
  border: 2px solid #f68251;
  box-shadow: 0px 0px 20px -17px;
}

.input:active {
  transform: scale(0.98);
}

.input:focus {
  border: 2px solid grey;
}

.input_time {
  width: 150px;
  max-width: 320px;
  height: 45px;
  padding: 12px;
  margin-top: 5px;
  border-radius: 12px;
  border: 1.5px solid #f68251;
  outline: none;
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
  box-shadow: 0px 0px 20px -18px;
}

.input_time:hover {
  border: 2px solid #f68251;
  box-shadow: 0px 0px 20px -17px;
}

.input_time:active {
  transform: scale(0.98);
}

.input_time:focus {
  border: 2px solid grey;
}

.input_edit {
  width: 400px;
  max-width: 320px;
  height: 150px;
  padding: 12px;
  margin-top: 5px;
  border-radius: 12px;
  border: 1.5px solid #f68251;
  outline: none;
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
  box-shadow: 0px 0px 20px -18px;
}
.input_edit:hover {
  border: 2px solid #f68251;
  box-shadow: 0px 0px 20px -17px;
}
.input_edit:focus {
  border: 2px solid grey;
}

.input_edit::-webkit-scrollbar {
  width: 10px;
}

.input_edit::-webkit-scrollbar-track {
  background-color: rgb(236, 236, 236);
  border-radius: 10px;
}

.input_edit::-webkit-scrollbar-thumb {
  background-color: #868686;
  border-radius: 8px;
  border: 2px solid rgb(236, 236, 236);
}

.btn_upd {
  border-radius: 999px;
  padding: 1em 2em;
  background: linear-gradient(117deg, #fbefbb, #f68251);
  border: none;
  color: #000000;
  font-size: 1rem;
  text-transform: uppercase;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: box-shadow 0.25s ease 0s;
  letter-spacing: 0.08rem;
}

.btn_upd:hover,
.btn_upd:focus {
  background: linear-gradient(117deg, #fbefbb, #f68251);
  box-shadow: 0 0.1rem 0.8rem rgb(254, 100, 70);
}

.btn_upload {
  border-radius: 30px;
  padding: 0.8em 2em;
  background: linear-gradient(117deg, #fbefbb, #f68251);
  border: none;
  color: #000000;
  font-size: 1rem;
  text-transform: uppercase;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: box-shadow 0.25s ease 0s;
